// Main JavaScript entry point

// Import CSS
import '../css/main.scss';

(function() {
  'use strict';

  // ---------------------------------------------------------------
  // Header Scroll Effect
  // ---------------------------------------------------------------
  function initHeaderScroll() {
    const header = document.querySelector('.header');
    if (!header) return;

    let lastScroll = 0;

    window.addEventListener('scroll', () => {
      const currentScroll = window.pageYOffset;

      if (currentScroll > 50) {
        header.classList.add('scrolled');
      } else {
        header.classList.remove('scrolled');
      }

      lastScroll = currentScroll;
    });
  }

  // ---------------------------------------------------------------
  // Mobile Menu Toggle
  // ---------------------------------------------------------------
  function initMobileMenu() {
    const menuToggle = document.getElementById('menuToggle');
    const menu = document.querySelector('.menu');

    if (!menuToggle || !menu) return;

    menuToggle.addEventListener('click', () => {
      menu.classList.toggle('open');
      menuToggle.setAttribute('aria-expanded',
        menu.classList.contains('open') ? 'true' : 'false'
      );
    });

    // Close menu when clicking outside
    document.addEventListener('click', (e) => {
      if (!menu.contains(e.target) && !menuToggle.contains(e.target)) {
        menu.classList.remove('open');
        menuToggle.setAttribute('aria-expanded', 'false');
      }
    });

    // Close menu when window is resized to desktop
    let resizeTimer;
    window.addEventListener('resize', () => {
      clearTimeout(resizeTimer);
      resizeTimer = setTimeout(() => {
        if (window.innerWidth > 768) {
          menu.classList.remove('open');
          menuToggle.setAttribute('aria-expanded', 'false');
        }
      }, 250);
    });
  }

  // ---------------------------------------------------------------
  // Scroll to Top Button
  // ---------------------------------------------------------------
  function initScrollTop() {
    const scrollTopButton = document.getElementById('scrollTop');
    if (!scrollTopButton) return;

    const showOnScroll = 400; // Show button after scrolling 400px

    window.addEventListener('scroll', () => {
      if (window.pageYOffset > showOnScroll) {
        scrollTopButton.classList.add('visible');
      } else {
        scrollTopButton.classList.remove('visible');
      }
    });

    scrollTopButton.addEventListener('click', () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    });
  }

  // ---------------------------------------------------------------
  // Smooth Scroll for Anchor Links
  // ---------------------------------------------------------------
  function initSmoothScroll() {
    // Add smooth scroll class to html
    document.documentElement.classList.add('smooth-scroll');

    // Smooth scroll for anchor links
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
      anchor.addEventListener('click', function (e) {
        const href = this.getAttribute('href');

        // Skip if href is just "#"
        if (href === '#') return;

        const target = document.querySelector(href);
        if (target) {
          e.preventDefault();

          const headerOffset = 80;
          const elementPosition = target.getBoundingClientRect().top;
          const offsetPosition = elementPosition + window.pageYOffset - headerOffset;

          window.scrollTo({
            top: offsetPosition,
            behavior: 'smooth'
          });

          // Update URL without scrolling
          if (history.pushState) {
            history.pushState(null, null, href);
          }
        }
      });
    });
  }

  // ---------------------------------------------------------------
  // External Links
  // ---------------------------------------------------------------
  function initExternalLinks() {
    const links = document.querySelectorAll('a[href^="http"]');
    const currentDomain = window.location.hostname;

    links.forEach(link => {
      const linkDomain = new URL(link.href).hostname;

      if (linkDomain !== currentDomain) {
        link.setAttribute('target', '_blank');
        link.setAttribute('rel', 'noopener noreferrer');

        // Add external link icon (optional)
        // const icon = document.createElement('span');
        // icon.innerHTML = ' â†—';
        // icon.style.fontSize = '0.8em';
        // link.appendChild(icon);
      }
    });
  }

  // ---------------------------------------------------------------
  // Code Copy Buttons (simple implementation)
  // ---------------------------------------------------------------
  function initCodeCopy() {
    const codeBlocks = document.querySelectorAll('pre code');

    codeBlocks.forEach(block => {
      const pre = block.parentElement;

      // Skip if button already exists
      if (pre.querySelector('.copy-button')) return;

      // Create copy button
      const button = document.createElement('button');
      button.className = 'copy-button';
      button.textContent = 'Copy';
      button.setAttribute('aria-label', 'Copy code to clipboard');

      // Position container
      pre.style.position = 'relative';

      // Button styles
      Object.assign(button.style, {
        position: 'absolute',
        top: '8px',
        right: '8px',
        padding: '4px 12px',
        fontSize: '12px',
        backgroundColor: 'var(--bg-secondary)',
        color: 'var(--text-primary)',
        border: '1px solid var(--border)',
        borderRadius: 'var(--radius-sm)',
        cursor: 'pointer',
        opacity: '0',
        transition: 'opacity var(--transition-fast)',
      });

      // Show button on hover
      pre.addEventListener('mouseenter', () => {
        button.style.opacity = '1';
      });

      pre.addEventListener('mouseleave', () => {
        if (!button.classList.contains('copied')) {
          button.style.opacity = '0';
        }
      });

      // Copy functionality
      button.addEventListener('click', async () => {
        const code = block.textContent;

        try {
          await navigator.clipboard.writeText(code);
          button.textContent = 'Copied!';
          button.classList.add('copied');

          setTimeout(() => {
            button.textContent = 'Copy';
            button.classList.remove('copied');
            button.style.opacity = '0';
          }, 2000);
        } catch (err) {
          console.error('Failed to copy code:', err);
          button.textContent = 'Error';

          setTimeout(() => {
            button.textContent = 'Copy';
          }, 2000);
        }
      });

      pre.appendChild(button);
    });
  }

  // ---------------------------------------------------------------
  // Share Link Copy
  // ---------------------------------------------------------------
  function initShareCopy() {
    const copyLinkButton = document.getElementById('copy-link-button');
    if (!copyLinkButton) return;

    copyLinkButton.addEventListener('click', async () => {
      const url = window.location.href;

      try {
        await navigator.clipboard.writeText(url);
        copyLinkButton.classList.add('copied');

        setTimeout(() => {
          copyLinkButton.classList.remove('copied');
        }, 2000);
      } catch (err) {
        console.error('Failed to copy link:', err);
        // Fallback for older browsers
        const textArea = document.createElement('textarea');
        textArea.value = url;
        document.body.appendChild(textArea);
        textArea.select();
        try {
          document.execCommand('copy');
          copyLinkButton.classList.add('copied');
          setTimeout(() => {
            copyLinkButton.classList.remove('copied');
          }, 2000);
        } catch (err2) {
          console.error('Fallback copy failed:', err2);
        }
        document.body.removeChild(textArea);
      }
    });
  }

  // ---------------------------------------------------------------
  // Initialize all features
  // ---------------------------------------------------------------
  function init() {
    initHeaderScroll();
    initMobileMenu();
    initScrollTop();
    initSmoothScroll();
    initExternalLinks();
    initCodeCopy();
    initShareCopy();
  }

  // Run when DOM is ready
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', init);
  } else {
    init();
  }
})();
